@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 70%;
        height: 100vh;
        padding: 20px 15px;
        background-color: #000503f1;
        transition: 0.5s all;
        &_active {
            left: 0; }
        .menu_item {
            padding-right: 0;
            border: 0;
            margin: 10px;
            margin-top: 20px;
            margin-bottom: 20px; }
        .menu_link {
            font-size: 30px;
            font-weight: 400; } }
    .hamburger {
        display: block;
        position: fixed;
        right: 30px;
        top: 30px;
        transform: translateY(-50%);
        width: 40px;
        height: 26px;
        span {
            display: block;
            height: 4px;
            border-radius: 3px;
            width: 100%;
            background-color: #0ec583;
            margin-bottom: 6px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(5px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(5px) rotate(45deg); } } } }
